import React, { useEffect, useState } from "react";
import {
  Button,
  CssBaseline,
  Stack,
  Box,
  Typography,
  Container,
  createTheme,
  ThemeProvider,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Grid,
} from "@mui/material";
import Navbar from "../Layout/Header";
import Image from "../image/img3.png";
import Swal from "sweetalert2";
import instance from "../axios.create";
import dayjs from "dayjs";
import { DemoItem } from "@mui/x-date-pickers/internals/demo";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import SearchIcon from "@mui/icons-material/Search";
import { MobileDatePicker } from "@mui/x-date-pickers/MobileDatePicker";

import "dayjs/locale/th";

import Room1 from "../image/room1.jpg";
const styles = {
  paperContainer: {
    backgroundImage: `url(${Image})`,
    backgroundPosition: "center",
  },
};

const theme = createTheme();
const token = localStorage.getItem("token");

export default function Superior() {
  const [rows, setrows] = useState([]);
  const [selectedDate, setSelectedDate] = useState(dayjs(new Date()));
  const [selectedDateout, setSelectedDateout] = useState();
  const [roomname, setroomname] = useState();

  var tomorrow = new Date(selectedDate);
  tomorrow.setDate(tomorrow.getDate() + 1);

  sessionStorage.removeItem("roomkey");

  useEffect(() => {
    
    sessionStorage.setItem("checkout", selectedDateout);
    sessionStorage.setItem("checkin", selectedDate);
    console.log(selectedDate)
   
    const Data = {
      id_Type: sessionStorage.getItem("id_Type"),
    };
    instance.post("roomtype", Data).then((response) => {
      
      setroomname(response.data[0].nameroom);
    });
    
    const token = localStorage.getItem("token");
    instance
      .post(
        "authen",
        {},
        {
          headers: {
            Authorization: `Basic ${token}`,
          },
        }
      )
      .then((response) => {
        if (response.data.status === "ok") {
        } else {
          // Swal.fire({
          //   title: 'Authen Time Out',
          //   text: 'Please Login agin',
          //   icon: 'warning',
          //   confirmButtonText: 'OK'
          // }).then((result) => {
          //   if (result.isConfirmed) {
          //     localStorage.removeItem("token");
          //   }
          // });
        }
      });
      
  }, []);
  
  function check(temp) {
    const checkStatus = temp;
    const buttonColor = checkStatus ? "error" : "success";
    return buttonColor;
  }

  function check1(temp, price) {
    const checkStatus = temp;
    const Buttonname = checkStatus
      ? ""
      : "  " + price + "/";
    return Buttonname;
  }

  const handleClick = (param, id) => {
    if (param === 1) {
      Swal.fire({
        title: "",
        text: " ",
        icon: "warning",
        confirmButtonText: "OK",
      });
    } else {
      if (token === null) {
        Swal.fire({
          title: "",
          text: "",
          icon: "warning",
          confirmButtonText: "OK",
        });
      } else if (selectedDateout <= selectedDate) {
        Swal.fire({
          title: "",
          text: " Check out ",
          icon: "warning",
          confirmButtonText: "OK",
        });
      } else {
        sessionStorage.setItem("roomkey", id);

        window.location = "/pay1";
      }
    }
  };

  function handle() {

    if (sessionStorage.getItem("checkout") === "undefined") {
      Swal.fire({
        title: "",
        text: " Check in  Check out",
        icon: "warning",
        confirmButtonText: "OK",
      });

    }else if (selectedDateout <= selectedDate) {
      Swal.fire({
        title: "",
        text: " Check out ",
        icon: "warning",
        confirmButtonText: "OK",
      });
    } else{
      const JsonData = {
      id_Type: sessionStorage.getItem("id_Type"),
      nameroom: sessionStorage.getItem("nameroom"),
      checkin: sessionStorage.getItem("checkin"),
      checkout: sessionStorage.getItem("checkout"),
    };
    instance.post("check",JsonData).then((response) => {
      setrows(response.data);
      console.log(response.data);
    });
    }
    
  }
  const color = "#14274A";
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />

      <main>
        {/* Hero unit */}
        <Box style={styles.paperContainer}>
          <Navbar />
          <Box
            color="transparent"
            sx={{
              pt: 10,
              pb: 10,
            }}
          >
            <Container maxWidth="sm">
              <Typography
                variant="h5"
                align="center"
                color="common.white"
                paragraph
              >
                WELCOME TO
              </Typography>
              <Typography
                component="h1"
                variant="h2"
                align="center"
                color="common.white"
                gutterBottom
              >
                {roomname}
              </Typography>
              <Typography
                variant="h5"
                align="center"
                color="common.white"
                paragraph
              >
                Book your stay and enjoy Luxury redefined at the most affordable
                rates.
              </Typography>
              <Stack
                sx={{ pt: 4 }}
                direction="row"
                spacing={2}
                justifyContent="center"
              ></Stack>
            </Container>
          </Box>
        </Box>

        <Container sx={{ py: 1 ,mt: 3}} maxWidth="md">
          <Box sx={{ display: 'flex', alignItems: 'center' , justifyContent: 'center' }}>
            <LocalizationProvider
              dateAdapter={AdapterDayjs}
              adapterLocale={"th"}
            >
              <DemoItem label="Check in">
                <MobileDatePicker
                  minDate={dayjs(new Date())}
                  onChange={(date) => {
                    setSelectedDate(date);
                    const test = new Date(date);
                    sessionStorage.setItem("checkin", test);
                  }}
                  value={selectedDate}
                  sx={{ pr: 1 }}
                />
              </DemoItem>
              <DemoItem label="Check out">
                <MobileDatePicker
                  minDate={dayjs(tomorrow)}
                  onChange={(date) => {
                    const test = new Date(date);
                    // test.setDate(test.getDate() + 1);
                    setSelectedDateout(test);
                    sessionStorage.setItem("checkout", test);

                  }}
                />
              </DemoItem>
              <Button
                variant="contained"
                startIcon={<SearchIcon />}
                sx={{ height: "100%", ml: 1, borderRadius: 5 , mt: 3}}
                onClick={handle}

              >
                Search
              </Button>
              
            </LocalizationProvider>
          </Box>
          <Grid
            container
            spacing={4}
            sx={{
              pt: 5,
            }}
          >
            {rows.map((row) => (
              <Grid item key={row.id_room} xs={12} sm={12} md={12}>
                <Card
                  sx={{
                    height: "100%",
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <CardMedia component="img" image={Room1} alt="random" />
                  <CardContent
                    sx={{
                      flexGrow: 1,
                      bgcolor: color,
                      textAlign: "center",
                      color: "#f0f0f0",
                    }}
                  >
                    <Typography gutterBottom variant="h5" component="h2">
                      {row.name_room}
                    </Typography>
                  </CardContent>
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography>{row.detill_room}</Typography>
                  </CardContent>
                  <CardActions disableSpacing>
                    <Button
                      variant="contained"
                      color={check(row.status)}
                      onClick={() => handleClick(row.status, row.id_room)}
                    >
                      {check1(row.status, row.price)}
                    </Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </main>
      {/* Footer */}

      {/* End footer */}
    </ThemeProvider>
  );
}
