import Typography from "@mui/material/Typography";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import { CardActionArea } from "@mui/material";
import Room1 from "../image/room1.jpg";
import React, { useEffect, useState } from "react";
import instance from "../axios.create";

export default function Roomdetail() {
  const [rows, setrows] = useState([]);

  // const nameroom = sessionStorage.getItem('nameroom');
  // const roomkey = sessionStorage.getItem('roomkey');
  const checkin = new Date(sessionStorage.getItem("checkin"));
  const checkout = new Date(sessionStorage.getItem("checkout"));

  const dayOfWeekin = checkin.getDay();
  const dayOfWeekout = checkout.getDay();

  const days = [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
  ];
  function test() {
    const T1 = new Date(checkin);
    var count = 0;
    while (T1 <= checkout) {
      T1.setDate(T1.getDate() + 1);
      count++;
    }
    return count;
  }

  useEffect(() => {
    test();
    const dataJson = {
      id_Type: sessionStorage.getItem("id_Type"),
      roomkey: sessionStorage.getItem("roomkey"),
    };
    instance.post("payroom", dataJson).then((response) => {
      setrows(response.data);
    });
  }, []);

  return (
    <React.Fragment>
      {rows.map((row) => (
        <Card
          key={row.id_room}
          variant="outlined"
          sx={{
            height: "100%",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <CardActionArea>
            <CardMedia
              component="img"
              height="140"
              image={Room1}
              alt="green iguana"
            />

            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                {row.name_room}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {row.detill_room}
              </Typography>
              <Typography level="body3" sx={{ py: 2 }}>
                Check in{" "}
                {days[dayOfWeekin] +
                  " " +
                  checkin.getDate() +
                  "/" +
                  checkin.getMonth() +
                  "/" +
                  checkin.getFullYear()}
              </Typography>
              <Typography level="body3" sx={{ py: 1 }}>
                Check out{" "}
                {days[dayOfWeekout] +
                  " " +
                  checkout.getDate() +
                  "/" +
                  checkout.getMonth() +
                  "/" +
                  checkout.getFullYear()}
              </Typography>
              <Typography level="body3" sx={{ py: 1 }}>
                Price Total {row.price * test()}
                {sessionStorage.setItem("price", row.price * test())}
              </Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      ))}
    </React.Fragment>
  );
}
