import React, { useEffect, useState } from "react";
// import Link from "@mui/material/Link";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import Modal from "@mui/material/Modal";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import { Button } from "@mui/material";
import instance from "../axios.create";
import DeleteIcon from "@mui/icons-material/Delete";
import { Box } from "@mui/system";
import TextField from "@mui/material/TextField";
import Swal from "sweetalert2";

const style_ = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  borderRadius: 4,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
};

export default function Roomdata() {
  const [type, settype] = useState([]);
  const [open, setOpen] = useState(false);
  const [addon, setaddon] = useState(false);

  const handleClose = () => setOpen(false);

  const [ID_Type, setID_Type] = useState("");
  const [Type_Name, setType_Name] = useState("");
  const [Detill_Type, setDetill_Type] = useState("");
  const [Path_Type, setPath_Type] = useState("");

  useEffect(() => {
    instance.get("typeroom").then((response) => {
      settype(response.data);
      console.log(response.data);
    });
  }, []);
  const handleClick = (id, name, title, path) => {
    setOpen(true);
    setaddon(false);

    setID_Type(id);
    setType_Name(name);
    setDetill_Type(title);
    setPath_Type(path);
  };

  const deleteClick = (id) => {
    Swal.fire({
      title: '',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: ' ',
      cancelButtonText: ' '
    }).then((result) => {
      if (result.isConfirmed) {
        instance.delete(`delete_typeroom/${id}`).then((response) => {
          if (response.data.status === "ok") {
            Swal.fire({
              title: "Delete Type Room Success",
              icon: "success",
              showConfirmButton: false,
            });
            setTimeout(function () {
              window.location.reload();
            }, 1000);
          }
        });
      }
    })
   
  };

  const Clickadd = (temp) => {
    if (temp === 1) {
      setaddon(true);
      setOpen(true);

      setID_Type("");
      setType_Name("");
      setDetill_Type("");
      setPath_Type("");
    } else if (temp === 2) {
      setOpen(false);
      const JsonData = {
        nameroom: Type_Name,
        title: Detill_Type,
        path: Path_Type,
      };
      instance.post("add_typeroom", JsonData).then((response) => {
        if (response.data.status === "ok") {
          Swal.fire({
            title: "Add Type Room Success",
            icon: "success",
            showConfirmButton: false,
          });
          setTimeout(function () {
            window.location.reload();
          }, 1000);
        }
      });
    }
  };

  const Clickupdate = () => {
    setOpen(false);
    const update_room = {
      id: ID_Type,
      nameroom: Type_Name,
      title: Detill_Type,
      path: Path_Type,
    };
    instance.put("update_type", update_room).then((response) => {
      if (response.data.status === "ok") {
        Swal.fire({
          title: "Update Type Room Success",
          icon: "success",
          showConfirmButton: false,
        });
        setTimeout(function () {
          window.location.reload();
        }, 1000);
      }
    });
  };
  return (
    <React.Fragment>
      <Box
        sx={{ display: "flex", justifyContent: "flex-end", p: 2 }}
        onClick={() => {
          Clickadd(1);
        }}
      >
        <Button
          variant="contained"
          color="success"
          sx={{ borderRadius: 5 }}
          // onClick={() => handleClick(row.password_room)}
        >
          
        </Button>
      </Box>

      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>ID_Room</TableCell>
            <TableCell sx={{ pr: 13 }}>Type_Name</TableCell>
            <TableCell>Detill_Type</TableCell>
            <TableCell>Path</TableCell>
            <TableCell>Edit</TableCell>
            <TableCell>Delete</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {type.map((row) => (
            <TableRow key={row.id}>
              <TableCell>{row.id}</TableCell>
              <TableCell>{row.nameroom}</TableCell>
              <TableCell>{row.title}</TableCell>
              <TableCell>{row.path}</TableCell>
              <TableCell>
                <Button
                  variant="contained"
                  color="secondary"
                  sx={{ borderRadius: 5 }}
                  onClick={() =>
                    handleClick(row.id, row.nameroom, row.title, row.path)
                  }
                >
                  
                </Button>
              </TableCell>
              <TableCell>
                <Button
                  variant="contained"
                  color="error"
                  sx={{ borderRadius: 5 }}
                  endIcon={<DeleteIcon />}
                  onClick={() => deleteClick(row.id)}
                >
                  
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <Modal open={open} onClose={handleClose}>
        <Box sx={style_}>
          <TextField
            id="Type_Name"
            label="Type_Name"
            value={Type_Name}
            variant="outlined"
            margin="normal"
            required
            fullWidth
            onChange={(e) => setType_Name(e.target.value)}
          />

          <TextField
            id="Detill_Type"
            label="Detill_Type"
            value={Detill_Type}
            variant="outlined"
            margin="normal"
            multiline
            rows={4}
            fullWidth
            required
            onChange={(e) => setDetill_Type(e.target.value)}
          />
          <TextField
            id="Path_Type"
            label="Path_Type"
            value={Path_Type}
            variant="outlined"
            margin="normal"
            fullWidth
            required
            onChange={(e) => setPath_Type(e.target.value)}
          />
          <Box>
            {!addon && (
              <Button
                variant="contained"
                color="secondary"
                sx={{ borderRadius: 5, mr: 5 }}
                onClick={() => {
                  Clickupdate();
                }}
              >
                
              </Button>
            )}
            {addon && (
              <Button
                variant="contained"
                color="secondary"
                sx={{ borderRadius: 5, mr: 5 }}
                onClick={() => {
                  Clickadd(2);
                }}
              >
                
              </Button>
            )}

            <Button
              variant="contained"
              color="error"
              sx={{ borderRadius: 5 }}
              onClick={() => setOpen(false)}
            >
              
            </Button>
          </Box>
        </Box>
      </Modal>
      <style>{`
        table {
          border-collapse: separate;
          border-spacing: 0;
          min-width: 800px;
          width: 100%;
        }
        th {
          background-color: #f5f5f5;
          border: 1px solid #ddd;
          font-weight: bold;
          padding: 10px;
          text-align: left;
        }
        td {
          border: 1px solid #ddd;
          padding: 10px;
        }
        tr:nth-child(even) td {
          background-color: #f5f5f5;
        }
        tr:hover td {
          background-color: #fafafa;
        }
      `}</style>
    </React.Fragment>
  );
}
