import React, { useEffect, useState } from "react";
import instance from "../axios.create";
import { MobileDatePicker } from "@mui/x-date-pickers/MobileDatePicker";
import { DemoItem } from "@mui/x-date-pickers/internals/demo";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import dayjs from "dayjs";
import { FormControl, Select, MenuItem } from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import Swal from "sweetalert2";
import {
  Button,
  Box,
  Typography,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Grid,
} from "@mui/material";
import Room1 from "../image/room1.jpg";

const color = "#14274A";

export default function Userdata() {
  const [rows, setrows] = useState([]);
  const [selectedType, setSelectedType] = useState("");
  const [type, settype] = useState([]);
  const [selectedDate, setSelectedDate] = useState(dayjs(new Date()));
  const [selectedDateout, setSelectedDateout] = useState();
  const [show, setSelectedshow] = useState(false);

  var tomorrow = new Date(selectedDate);
  tomorrow.setDate(tomorrow.getDate() + 1);
  const token = localStorage.getItem("token");
  sessionStorage.setItem("activeStep", 0);

  useEffect(() => {
    sessionStorage.setItem("checkout", selectedDateout);
    sessionStorage.setItem("checkin", selectedDate);

    // instance.post("room_lsit").then((response) => {
    //   setrows(response.data);
    //   console.log(response.data);
    // });
    instance.get("typeroom").then((response) => {
      settype(response.data);
      console.log(response.data);
    });
  }, []);

  function test() {
    const T1 = new Date(selectedDate);
    var count = 0;
    while (T1 <= selectedDateout) {
      T1.setDate(T1.getDate() + 1);
      count++;
    }
    return count;
  }
  const handleSelectType = (id) => {
    setSelectedType(id);
    sessionStorage.setItem("nameroom",type[id-1].path);
    sessionStorage.setItem("id_Type", id);

  };

  function handle() {
    if (sessionStorage.getItem("checkout") === "undefined") {
      Swal.fire({
        title: "",
        text: " Check in  Check out",
        icon: "warning",
        confirmButtonText: "OK",
      });
    } else if (selectedDateout <= selectedDate) {
      Swal.fire({
        title: "",
        text: " Check out ",
        icon: "warning",
        confirmButtonText: "OK",
      });
    } else if (selectedType === "") {
      Swal.fire({
        title: "",
        icon: "warning",
        confirmButtonText: "OK",
      });
    }else {
      setSelectedshow(true);

      const JsonData = {
        id_Type: sessionStorage.getItem("id_Type"),
        nameroom: sessionStorage.getItem("nameroom"),
        checkin: sessionStorage.getItem("checkin"),
        checkout: sessionStorage.getItem("checkout"),
      };
      instance.post("check", JsonData).then((response) => {
        setrows(response.data);
        console.log(response.data);
      });
    }
  }
  function check(temp) {
    const checkStatus = temp;
    const buttonColor = checkStatus ? "error" : "success";
    return buttonColor;
  }

  function check1(temp, price) {
    const checkStatus = temp;
    const Buttonname = checkStatus
      ? ""
      : "  " + price + "/";
    return Buttonname;
  }

  const handleClick = (param, id, price) => {
    if (param === 1) {
      Swal.fire({
        title: "",
        text: " ",
        icon: "warning",
        confirmButtonText: "OK",
      });
    } else {
      if (token === null) {
        Swal.fire({
          title: "",
          text: "",
          icon: "warning",
          confirmButtonText: "OK",
        });
      } else if (selectedDateout <= selectedDate) {
        Swal.fire({
          title: "",
          text: " Check out ",
          icon: "warning",
          confirmButtonText: "OK",
        });
      } else {
        sessionStorage.setItem("price", price * test())
        sessionStorage.setItem("roomkey", id);
        sessionStorage.setItem("activeStep", + 1);
        window.location.reload()
      }
    }
  };
  return (
    <React.Fragment>
      <Box
        sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}
      >
        <FormControl sx={{  mr: 5, minWidth: 200 }}>
        <DemoItem label="">
          <Select
            id="room-select"
            value={selectedType}
            onChange={(e) => handleSelectType(e.target.value)}
          >
            {type.map((room) => (
              <MenuItem key={room.id} value={room.id}>
                {room.nameroom}
              </MenuItem>
            ))}
          </Select>
          </DemoItem>

        </FormControl>
        <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale={"th"}>
          <DemoItem label="">
            <MobileDatePicker
              minDate={dayjs(new Date())}
              onChange={(date) => {
                setSelectedDate(date);
                const test = new Date(date);
                sessionStorage.setItem("checkin", test);
              }}
              value={selectedDate}
              sx={{ pr: 1 }}
            />
          </DemoItem>
          <DemoItem label="">
            <MobileDatePicker
              minDate={dayjs(tomorrow)}
              onChange={(date) => {
                const test = new Date(date);
                // test.setDate(test.getDate() + 1);
                setSelectedDateout(test);
                sessionStorage.setItem("checkout", test);
              }}
            />
          </DemoItem>
          <Button
            variant="contained"
            startIcon={<SearchIcon />}
            sx={{ height: "100%", ml: 1, borderRadius: 5, mt: 3 }}
            onClick={handle}
          >
            Search
          </Button>
        </LocalizationProvider>
      </Box>
      <Grid
            container
            spacing={4}
            sx={{
              pt: 5,
            }}
          >
          {show === false ? "" :  rows.map((row) => (
              <Grid item key={row.id_room} xs={12} sm={6} md={4}>
                <Card
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <CardMedia component="img" image={Room1} alt="random" />
                  <CardContent
                    sx={{
                      flexGrow: 1,
                      bgcolor: color,
                      textAlign: "center",
                      color: "#f0f0f0",
                    }}
                  >
                    <Typography gutterBottom variant="h6" component="h2">
                      {row.name_room}
                    </Typography>
                  </CardContent>
                  <CardContent>
                    <Typography variant="subtitle1">{row.detill_room}</Typography>
                  </CardContent>
                  <CardActions disableSpacing>
                    <Button
                      variant="contained"
                      color={check(row.status)}
                      onClick={() => handleClick(row.status, row.id_room, row.price)}
                    >
                      {check1(row.status, row.price)}
                    </Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}     
            
           
          </Grid>
          
    </React.Fragment>
  );
}
