import React, { useEffect, useState } from "react";
import { styled, createTheme, ThemeProvider } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import MuiDrawer from "@mui/material/Drawer";
import Box from "@mui/material/Box";
import MuiAppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import List from "@mui/material/List";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import MenuIcon from "@mui/icons-material/Menu";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import { mainListItems, secondaryListItems , loginprofile , adminconsole } from "./Menubar";
import { useLocation } from "react-router-dom";
import Userdata from "./Userdata";
import Roomrdata from "./Roomdata";
import RoomList from "./RoomList";
import TypeList from "./TypeList";
import Employeelist from "./Employeelist";
import Walkin from "./Walkin";
import instance from "../axios.create";
import Swal from "sweetalert2";

const drawerWidth = 240;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(["width", "margin"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  "& .MuiDrawer-paper": {
    position: "relative",
    whiteSpace: "nowrap",
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    boxSizing: "border-box",
    ...(!open && {
      overflowX: "hidden",
      transition: theme.transitions.create("width", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      width: theme.spacing(7),
      [theme.breakpoints.up("sm")]: {
        width: theme.spacing(9),
      },
    }),
  },
}));

const mdTheme = createTheme();

function DashboardContent() {
  const [activeStep, setActiveStep] = useState(0);
  const [open, setOpen] = useState(true);
  const location = useLocation();
  const [auth, setAuth] = useState(true);

  function check(location) {
    const currentPath = location.pathname;
    if (currentPath === "/dashboard/userdata") {
      setActiveStep(0);
    }
    else if (currentPath === "/dashboard/roomdata") {
      setActiveStep(1);
    }
    else if (currentPath === "/dashboard/roomlist") {
      setActiveStep(2);
    }
    else if (currentPath === "/dashboard/typelist") {
      setActiveStep(3);
    }
    else if (currentPath === "/dashboard/employee") {
      setActiveStep(4);
    }
    else if (currentPath === "/dashboard/walk_check_in") {
      setActiveStep(5);
    }
  }

  const toggleDrawer = () => {
    setOpen(!open);
  };

  const stepContentMap = {
    0: <Userdata />,
    1: <Roomrdata />,
    2: <RoomList />,
    3: <TypeList />,
    4: <Employeelist />,
    5: <Walkin />,

  };

  useEffect(() => {
    check(location);
    const token = localStorage.getItem("token");

    instance.post("authen",{},
        {
          headers: {
            Authorization: `Basic ${token}`,
          },
        }
      )
      .then((response) => {
        if (response.data.status === "ok") {
          setAuth(true);
        } else {
          setAuth(false);
          Swal.fire({
            title: "Authen Time Out",
            text: "Please Login agin",
            icon: "warning",
            showConfirmButton: false,
          });
          setTimeout(function () {
            window.location = "/login";
          }, 1000);
        }
      });
  },[]);

 
  return (
    <ThemeProvider theme={mdTheme}>
      <Box sx={{ display: "flex" }}>
        <CssBaseline />
        <AppBar position="absolute" open={open}>
          <Toolbar
            sx={{
              pr: "24px", // keep right padding when drawer closed
            }}
          >
            <IconButton
              edge="start"
              color="inherit"
              aria-label="open drawer"
              onClick={toggleDrawer}
              sx={{
                marginRight: "36px",
                ...(open && { display: "none" }),
              }}
            >
              <MenuIcon />
            </IconButton>
            <Typography
              component="h1"
              variant="h6"
              color="inherit"
              noWrap
              sx={{ flexGrow: 1 }}
            >
              Dashboard
            </Typography>
          </Toolbar>
        </AppBar>
        <Drawer variant="permanent" open={open}>
          <Toolbar
            sx={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-end",
              px: [1],
            }}
          >
            
            <IconButton onClick={toggleDrawer}>
              <ChevronLeftIcon />
            </IconButton>
          </Toolbar>
          <Divider />
          <List component="nav">
            {mainListItems}
            <Divider sx={{ my: 1 }} />
            {adminconsole}
            <Divider sx={{ my: 1 }} />
            {!auth && ( secondaryListItems )}
            {auth && ( loginprofile )}
          </List>
        </Drawer>
        <Box
          component="main"
          sx={{
            backgroundColor: (theme) =>
              theme.palette.mode === "light"
                ? theme.palette.grey[100]
                : theme.palette.grey[900],
            flexGrow: 1,
            height: "100vh",
            overflow: "auto",
          }}
        >
          <Toolbar />
          <React.Fragment>
            <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
              <Grid container spacing={3}>
                {/* Recent Orders */}
                <Grid item xs={12}>
                  <Paper
                    sx={{ p: 2, }}
                  >
                    {stepContentMap[activeStep]}
                  </Paper>
                </Grid>
              </Grid>
            </Container>
          </React.Fragment>
        </Box>
      </Box>
    </ThemeProvider>
  );
}

//Test123456

export default function Dashboard() {
  return <DashboardContent />;
}
