import Typography from "@mui/material/Typography";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import { CardActionArea } from "@mui/material";
import Room1 from "../image/room1.jpg";
import React, { useState, useRef, useEffect } from "react";
import instance from "../axios.create";
import { format } from "date-fns";
import QRCode from "qrcode.react";
import Swal from 'sweetalert2'

export default function Roomdetail() {
  const [rows, setrows] = useState([]);
  const dataFetchedRef = useRef(false);

  // const nameroom = sessionStorage.getItem('nameroom');
  // const roomkey = sessionStorage.getItem('roomkey');
  const checkin = new Date(sessionStorage.getItem("checkin"));
  const checkout = new Date(sessionStorage.getItem("checkout"));
  const data_user = JSON.parse(sessionStorage.getItem("User_data"));
  const [randomNumber, setRandomNumber] = useState("");

  
  useEffect(() => {
    const dataJson = {
      id_Type: sessionStorage.getItem("id_Type"),
      roomkey: sessionStorage.getItem("roomkey"),
    };
    instance.post("payroom", dataJson).then((response) => {
      setrows(response.data);
    });
    if (dataFetchedRef.current) return;
    dataFetchedRef.current = true;
    insert();
  }, []);

  function insert() {
    const token = localStorage.getItem("token");
    const newNumber = Math.floor(Math.random() * 100000);
    setRandomNumber(newNumber.toString().padStart(5, "0"));
    const password_room = newNumber.toString().padStart(5, "0");
    console.log(data_user);
   
          const JsonData = {
            email: "walkin."+data_user.email,
            nameroom: sessionStorage.getItem("nameroom"),
            id_room: sessionStorage.getItem("roomkey"),
            checkin: sessionStorage.getItem("checkin"),
            checkout: sessionStorage.getItem("checkout"),
            price_total: sessionStorage.getItem("price"),
            password_room: password_room,
            status_room: 1,
          };
          instance.post("booking", JsonData).then((response) => {
            if (response.data.status === true) {
              instance.post("user_Walkin", data_user).then((res) => {
                if (res.data.status === "ok") {
                  Swal.fire({
                    title: 'Booking success',
                    icon: 'success',
                  })
                } else {
                  Swal.fire({
                    title: 'Update User walking failed',
                    icon: 'error',
                    confirmButtonText: 'OK'
                  })
                }
              });
            } else {
              Swal.fire({
                title: 'Booking failed',
                icon: 'error',
                confirmButtonText: 'OK'
              })
            }
          });
       
          // });
        
     
  }
  return (
    <React.Fragment>
      {rows.map((row) => (
        <Card
          key={row.id_room}
          variant="outlined"
          sx={{
            height: "50%",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <CardActionArea>
            <CardMedia
              component="img"
              height="140"
              image={Room1}
              alt="green iguana"
            />

            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                {row.name_room}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {row.detill_room}
              </Typography>
              <Typography level="body3" sx={{ py: 2 }}>
                Check in : {format(new Date(checkin), "eee dd/MM/yyyy")}
              </Typography>
              <Typography level="body3" sx={{ py: 1 }}>
                Check out : {format(new Date(checkout), "eee dd/MM/yyyy")}
              </Typography>
              <Typography level="body3" sx={{ py: 1 }}>
                Price Total {sessionStorage.getItem("price")}
              </Typography>
              <Typography level="body3" sx={{ py: 1 }}>
                Name User: {data_user.name} {" "} {data_user.lastname}
              </Typography>
              <Typography level="body3" sx={{ py: 1 }}>
                Email User {data_user.email}
              </Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      ))}
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <p>Room Password : {randomNumber}</p>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <QRCode value={randomNumber} />
      </div>
    </React.Fragment>
  );
}
