import React, { useEffect, useState } from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Navbar from "../Layout/Header";
import Image from "../image/img5.png";
import Button from "@mui/material/Button";
import Swal from "sweetalert2";
import instance from "../axios.create";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Grid from "@mui/material/Grid";
import Room1 from "../image/room1.jpg";
import Modal from "@mui/material/Modal";
import QRCode from "qrcode.react";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import ErrorIcon from "@mui/icons-material/Error";
import Footer from "../Layout/Footer";
import CssBaseline from "@mui/material/CssBaseline";

const style_ = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  borderRadius: 4,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
};
const styles = {
  paperContainer: {
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
};

const theme = createTheme();

export default function Profile() {
  const [rows, setrows] = useState([]);
  const [open, setOpen] = React.useState(false);
  const [randomNumber, setRandomNumber] = useState("");

  const handleClose = () => setOpen(false);

  useEffect(() => {
    const token = localStorage.getItem("token");
    instance
      .post(
        "authen",
        {},
        {
          headers: {
            Authorization: `Basic ${token}`,
          },
        }
      )
      .then((response) => {
        console.log(response);
        if (response.data.status === "ok") {
          const temp = response.data.decoded.email;
          const JsonData = {
            email: temp,
          };
          instance.post("myroom", JsonData).then((response) => {
            console.log(response.data);
            setrows(response.data);
          });
        } else {
          Swal.fire({
            title: "Authen Time Out",
            text: "Please Login agin",
            icon: "warning",
            showConfirmButton: false,
          });
          setTimeout(function () {
            window.location = "/home";
          }, 1000);
        }
      });
  }, []);
  const days = [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
  ];

  function check(temp) {
    const checkStatus = temp;
    const buttonColor = checkStatus ? "error" : "success";
    return buttonColor;
  }

  function check1(temp) {
    const checkStatus = temp;
    const Buttonname = checkStatus
      ? " "
      : " ";
    const Icon = checkStatus ? <ErrorIcon /> : <CheckCircleIcon />;
    return { Icon, Buttonname };
  }
  const handleClick = (param) => {
    setOpen(true);
    setRandomNumber(param);
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />

      <main>
        {/* Hero unit */}
        <Box style={styles.paperContainer}>
          <Navbar />
          <Box
            color="transparent"
            elevation={0}
            sx={{
              pt: 5,
              pb: 10,
            }}
          >
            <Typography
              variant="h2"
              align="center"
              color="common.white"
              gutterBottom
            >
              MY Room
            </Typography>
          </Box>
        </Box>

        <Box sx={{ px: 10, pb: 21, pt: 2, backgroundColor: "#14274A" }}>
          <Typography
            variant="h4"
            gutterBottom
            style={{ textAlign: "center", color: "white" }}
          >
            Booking History
          </Typography>
          {rows.length === 0 ? (
            <Typography
              variant="h5"
              gutterBottom
              style={{ textAlign: "center", color: "white", m: 5 }}
            >
              No rooms available
            </Typography>
          ) : (
            <Grid container spacing={2}>
              {rows.map((booking) => (
                <Grid item xs={12} sm={6} md={4} key={booking.id_book}>
                  <Card sx={{ height: "100%" }}>
                    <CardMedia
                      component="img"
                      height="150"
                      image={Room1}
                      alt={booking.name_room}
                    />
                    <CardContent>
                      <Typography
                        variant="h6"
                        gutterBottom
                        sx={{ fontWeight: "bold" }}
                      >
                        {booking.name_room}
                      </Typography>
                      <Typography variant="subtitle1" sx={{ mb: 2 }}>
                        Booking ID: {booking.id_book}
                      </Typography>
                      <Typography sx={{ mb: 2 }}>
                        {booking.detill_room}
                      </Typography>
                      <Typography variant="h6" sx={{ mb: 2 }}>
                        THB: {booking.price_room} / night
                      </Typography>
                      <Typography sx={{ mb: 2 }}>
                        <strong> Check in: </strong>
                        {days[new Date(booking.checkin_room).getDay()] +
                          " " +
                          new Date(booking.checkin_room).getDate() +
                          "/" +
                          new Date(booking.checkin_room).getMonth() +
                          "/" +
                          new Date(booking.checkin_room).getFullYear()}
                      </Typography>
                      <Typography sx={{ mb: 2 }}>
                        <strong> Check out: </strong>
                        {days[new Date(booking.checkout_room).getDay()] +
                          " " +
                          new Date(booking.checkout_room).getDate() +
                          "/" +
                          new Date(booking.checkout_room).getMonth() +
                          "/" +
                          new Date(booking.checkout_room).getFullYear()}
                      </Typography>
                      <Typography sx={{ mb: 2 }}>
                        <strong>Price Total: </strong>
                        {booking.price_total}
                      </Typography>
                    </CardContent>
                    <CardActions sx={{ justifyContent: "space-between" }}>
                      <Button color={check(booking.status_room)}>
                        {check1(booking.status_room).Icon}
                        {"   "}
                        {check1(booking.status_room).Buttonname}
                      </Button>
                      <Button
                        variant="contained"
                        sx={{ borderRadius: 5 }}
                        onClick={() => handleClick(booking.password_room)}
                      >
                        
                      </Button>
                    </CardActions>
                  </Card>
                </Grid>
              ))}
            </Grid>
          )}
          <Modal open={open} onClose={handleClose}>
            <Box sx={style_}>
              <Typography variant="h6" component="h2">
                password is = {randomNumber}
              </Typography>

              <QRCode value={randomNumber} />
            </Box>
          </Modal>
        </Box>
      </main>
      <Footer />
    </ThemeProvider>
  );
}
