import * as React from "react";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import RecentActorsIcon from "@mui/icons-material/RecentActors";
import PeopleIcon from "@mui/icons-material/People";
import AssignmentIcon from "@mui/icons-material/Assignment";
import RoomPreferencesIcon from '@mui/icons-material/RoomPreferences';
import DirectionsWalkIcon from '@mui/icons-material/DirectionsWalk';
import LogoutIcon from '@mui/icons-material/Logout';
import CollectionsBookmarkIcon from '@mui/icons-material/CollectionsBookmark';
import { Link as RouterLink } from "react-router-dom";
import { useHistory } from "react-router-dom";

const handlelogout = (event) => {
  localStorage.removeItem("token");
  window.location = "/home";
  event.preventDefault();
};

const handleClearSessionStorage = () => {
  sessionStorage.clear();
};
export const mainListItems = (
  <React.Fragment>
    <ListItemButton to="/dashboard/userdata" component={RouterLink}>
      <ListItemIcon>
        <RecentActorsIcon />
      </ListItemIcon>
      <ListItemText primary="User Data List" />
    </ListItemButton>
    <ListItemButton to="/dashboard/roomdata" component={RouterLink}>
      <ListItemIcon>
        <CollectionsBookmarkIcon />
      </ListItemIcon>
      <ListItemText primary="Room Booking List" />
    </ListItemButton>
    <ListItemButton to="/dashboard/roomlist" component={RouterLink}>
      <ListItemIcon>
        <RoomPreferencesIcon />
      </ListItemIcon>
      <ListItemText primary="Room List" />
    </ListItemButton>
    <ListItemButton to="/dashboard/typelist" component={RouterLink}>
      <ListItemIcon>
        <RoomPreferencesIcon />
      </ListItemIcon>
      <ListItemText primary="Type Room List" />
    </ListItemButton>
  </React.Fragment>
);

export const secondaryListItems = (
  <React.Fragment>
    <ListItemButton to="/login" component={RouterLink}>
      <ListItemIcon>
        <AssignmentIcon />
      </ListItemIcon>
      <ListItemText primary="Login" />
    </ListItemButton>
  </React.Fragment>
);

export const loginprofile = (
  <React.Fragment>
    <ListItemButton>
      <ListItemIcon>
        <LogoutIcon />
      </ListItemIcon>
      <ListItemText primary="Logout" onClick={handlelogout}/>
    </ListItemButton>
  </React.Fragment>
);

export const adminconsole = (
  <React.Fragment>
     <ListItemButton to="/dashboard/walk_check_in" onClick={handleClearSessionStorage} component={RouterLink}>
      <ListItemIcon>
        <DirectionsWalkIcon />
      </ListItemIcon>
      <ListItemText primary="Walk Check in" />
    </ListItemButton>
    <ListItemButton to="/dashboard/employee" component={RouterLink}>
      <ListItemIcon>
        <PeopleIcon />
      </ListItemIcon>
      <ListItemText primary="Employee List" />
    </ListItemButton>
  </React.Fragment>
);
