import React, { useEffect, useState } from "react";
// import Link from "@mui/material/Link";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import Modal from "@mui/material/Modal";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import { Button } from "@mui/material";
import instance from "../axios.create";
import DeleteIcon from "@mui/icons-material/Delete";
import { Box } from "@mui/system";
import TextField from "@mui/material/TextField";
import Swal from "sweetalert2";

const style_ = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  borderRadius: 4,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
};

export default function Roomdata() {
  const [rows, setrows] = useState([]);
  const [type, settype] = useState([]);
  const [open, setOpen] = useState(false);
  const [addon, setaddon] = useState(false);

  const handleClose = () => setOpen(false);

  const [ID_Room, setID_Room] = useState("");
  const [Room_Name, setRoom_Name] = useState("");
  const [Detill_Room, setDetill_Room] = useState("");
  const [Price_Room, setPrice_Room] = useState("");
  const [ID_Type, setID_Type] = useState("");

  useEffect(() => {
    instance.post("room_lsit").then((response) => {
      setrows(response.data);
      console.log(response.data);
    });
    instance.get("typeroom").then((response) => {
      settype(response.data);
      console.log(response.data);
    });
  }, []);
  const handleClick = (id, name, detill, price, type) => {
    setOpen(true);
    setaddon(false);
    setID_Room(id);
    setRoom_Name(name);
    setDetill_Room(detill);
    setPrice_Room(price);
    setID_Type(type);
  };

  const deleteClick = (id) => {
    Swal.fire({
      title: '',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: ' ',
      cancelButtonText: ' '
    }).then((result) => {
      if (result.isConfirmed) {
        instance.delete(`delete_room/${id}`).then((response) => {
          if (response.data.status === "ok") {
            Swal.fire({
              title: "Delete Room Success",
              icon: "success",
            });
            setTimeout(function () {
              window.location.reload();
            }, 1000);
          }
        });
      }
    })
    
  };
  const [selectedRoom, setSelectedRoom] = useState("");

  // Add a function to handle selecting a Room Name
  const handleSelectRoom = (id) => {
    setSelectedRoom(id);
  };

  const Clickaddroom = (temp) => {
    if (temp === 1) {
      setaddon(true);
      setOpen(true);
      setID_Room('');
      setRoom_Name('');
      setDetill_Room('');
      setPrice_Room('');
      setID_Type(1);
    } else if (temp === 2) {
      setOpen(false);
      const JsonData = {
        name_room: Room_Name,
        detill_room: Detill_Room,
        price: Price_Room,
        id_type: ID_Type,
      };
      instance.post("add_room",JsonData).then((response) => {
        if (response.data.status === "ok") {
          Swal.fire({
            title: "Add Room Success",
            icon: "success",
            showConfirmButton: false,
          });
          setTimeout(function () {
            window.location.reload();
          }, 1000);
        }
      });
    }
  };

  const Clickupdate = () => {
    setOpen(false);
    const update_room = {
      id_room: ID_Room,
      name_room: Room_Name,
      detill_room: Detill_Room,
      price: Price_Room,
      id_type: ID_Type,
    };
    instance.put("update_room", update_room).then((response) => {
      if (response.data.status === "ok") {
        Swal.fire({
          title: "Update Success",
          icon: "success",
          showConfirmButton: false,
        });
        setTimeout(function () {
          window.location.reload();
        }, 1000);
      }
    });
  };
  return (
    <React.Fragment>
      <Box
        sx={{ display: "flex", justifyContent: "flex-end" }}
        onClick={() => {
          Clickaddroom(1);
        }}
      >
        <Button
          variant="contained"
          color="success"
          sx={{ borderRadius: 5 }}
          // onClick={() => handleClick(row.password_room)}
        >
          
        </Button>
      </Box>
      <div>
        <label htmlFor="room-select">Select Room Name:</label>
        <select
          id="room-select"
          value={selectedRoom}
          onChange={(e) => handleSelectRoom(e.target.value)}
        >
          <option value="">All Rooms</option>
          {type.map((room) => (
            <option key={room.id} value={room.id}>
              {room.nameroom}
            </option>
          ))}
        </select>
      </div>

      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>ID_Room</TableCell>
            <TableCell sx={{ pr: 13 }}>Room_Name</TableCell>
            <TableCell>Detill_Room</TableCell>
            <TableCell>Price_Room</TableCell>
            <TableCell>ID_Type</TableCell>
            <TableCell>Edit</TableCell>
            <TableCell>Delete</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows
            .filter((row) => {
              // If no Room Name is selected, show all rows
              if (selectedRoom === "") {
                return true;
              }
              // Otherwise, only show rows with the selected Room Name
              console.log(row.id_type === selectedRoom);
              return row.id_type.toString() === selectedRoom;
            })
            .map((row) => (
              <TableRow key={row.id_room}>
                <TableCell>{row.id_room}</TableCell>
                <TableCell>{row.name_room}</TableCell>
                <TableCell>{row.detill_room}</TableCell>
                <TableCell>{row.price}</TableCell>
                <TableCell>{row.id_type}</TableCell>
                <TableCell>
                  <Button
                    variant="contained"
                    color="secondary"
                    sx={{ borderRadius: 5 }}
                    onClick={() =>
                      handleClick(
                        row.id_room,
                        row.name_room,
                        row.detill_room,
                        row.price,
                        row.id_type
                      )
                    }
                  >
                    
                  </Button>
                </TableCell>
                <TableCell>
                  <Button
                    variant="contained"
                    color="error"
                    sx={{ borderRadius: 5 }}
                    endIcon={<DeleteIcon />}
                    onClick={() =>
                      deleteClick(
                        row.id_room,
                      )
                    }
                  >
                    
                  </Button>
                </TableCell>
              </TableRow>
            ))}
        </TableBody>
      </Table>
      <Modal open={open} onClose={handleClose}>
        <Box sx={style_}>
          <TextField
            id="room_name"
            label="room_name"
            value={Room_Name}
            variant="outlined"
            margin="normal"
            required
            fullWidth
            onChange={(e) => setRoom_Name(e.target.value)}
          />

          <TextField
            id="detill_Room"
            label="detill_Room"
            value={Detill_Room}
            variant="outlined"
            margin="normal"
            multiline
            rows={4}
            fullWidth
            required
            onChange={(e) => setDetill_Room(e.target.value)}
          />
          <TextField
            id="price_room"
            label="price_room"
            value={Price_Room}
            variant="outlined"
            margin="normal"
            fullWidth
            required
            onChange={(e) => setPrice_Room(e.target.value)}
          />
          <div>
            <label htmlFor="room-select">Select Room Name:</label>
            <select
              id="room-select"
              value={ID_Type}
              onChange={(e) => setID_Type(e.target.value)}
            >
              {type.map((room) => (
                <option key={room.id} value={room.id}>
                  {room.nameroom}
                </option>
              ))}
            </select>
          </div>
          <TextField
            id="id_type"
            label="id_type"
            value={ID_Type}
            variant="outlined"
            margin="normal"
            fullWidth
            required
            onChange={(e) => setID_Type(e.target.value)}
            disabled={true}
          />
          <Box>
            {!addon && (
              <Button
                variant="contained"
                color="secondary"
                sx={{ borderRadius: 5, mr: 5 }}
                onClick={() => {
                  Clickupdate();
                }}
              >
                
              </Button>
            )}
            {addon && (
              <Button
                variant="contained"
                color="secondary"
                sx={{ borderRadius: 5, mr: 5 }}
                onClick={() => {
                  Clickaddroom(2);
                }}
              >
                
              </Button>
            )}

            <Button
              variant="contained"
              color="error"
              sx={{ borderRadius: 5 }}
              onClick={() => setOpen(false)}
            >
              
            </Button>
          </Box>
        </Box>
      </Modal>
      <style>{`
        table {
          border-collapse: separate;
          border-spacing: 0;
          min-width: 800px;
          width: 100%;
        }
        th {
          background-color: #f5f5f5;
          border: 1px solid #ddd;
          font-weight: bold;
          padding: 10px;
          text-align: left;
        }
        td {
          border: 1px solid #ddd;
          padding: 10px;
        }
        tr:nth-child(even) td {
          background-color: #f5f5f5;
        }
        tr:hover td {
          background-color: #fafafa;
        }
      `}</style>
    </React.Fragment>
  );
}
