import React, { useState } from "react";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepLabel from "@mui/material/StepLabel";
import Typography from "@mui/material/Typography";
import Wlakin1 from "./Walkin1";
import Walkin2 from "./Walkin2";
import Walkin3 from "./Walkin3";
import Walkin4 from "./Walkin4";
import Walkin5 from "./Walkin5";

import Paper from "@mui/material/Paper";
import Container from "@mui/material/Container";

const steps = [
  "",
  "",
  "",
  "",
  "",
];
const stepContentMap = {
  0: <Wlakin1 />,
  1: <Walkin2 />,
  2: <Walkin3 />,
  3: <Walkin4 />,
  4: <Walkin5 />,
};
export default function Userdata() {
  const [activeStep, setActiveStep] = useState(
    parseInt(sessionStorage.getItem("activeStep")) || 0
  );
  console.log(activeStep);
  console.log(activeStep);
  if (isNaN(activeStep)) {
    sessionStorage.setItem("activeStep", 0);

    setActiveStep(0);
  } else if (activeStep === 5) {
    sessionStorage.setItem("activeStep", 0);
    window.location.reload();
  }
  const handleNext = () => {
    sessionStorage.setItem("activeStep", activeStep + 1);
    window.location.reload();
  };

  const handleBack = () => {
    sessionStorage.setItem("activeStep", activeStep - 1);
    window.location.reload();
  };

  return (
    <React.Fragment>
      <Container sx={{ py: 1 }} maxWidth="md">
        <Container component="main" maxWidth="xl" sx={{ mb: 4 }}>
          <Paper
            variant="outlined"
            sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}
          >
            <Typography component="h1" variant="h4" align="center">
              
            </Typography>
            <Stepper activeStep={activeStep} sx={{ pt: 3, pb: 5 }}>
              {steps.map((label) => (
                <Step key={label}>
                  <StepLabel>{label}</StepLabel>
                </Step>
              ))}
            </Stepper>
            {activeStep === steps.length ? (
              <React.Fragment>
                <Typography variant="h5" gutterBottom>
                  Thank you for your order.
                </Typography>
                <Typography variant="subtitle1">
                  Your order number is #2001539. We have emailed your order
                  confirmation, and will send you an update when your order has
                  shipped.
                </Typography>
              </React.Fragment>
            ) : (
              <React.Fragment>
                {stepContentMap[activeStep]}
                <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                  {activeStep !== 0 && (
                    <Button onClick={handleBack} sx={{ mt: 3, ml: 1 }}>
                      Back
                    </Button>
                  )}
                  {activeStep >= 2 && (
                    <Button
                      variant="contained"
                      onClick={handleNext}
                      sx={{ mt: 3, ml: 1 }}
                    >
                      {activeStep === steps.length - 1 ? "" : "Next"}
                    </Button>
                  )}
                </Box>
              </React.Fragment>
            )}
          </Paper>
        </Container>
      </Container>
    </React.Fragment>
  );
}
