import React, { useEffect, useState } from "react";
// import Link from "@mui/material/Link";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import Modal from "@mui/material/Modal";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import { Button } from "@mui/material";
import instance from "../axios.create";
import DeleteIcon from "@mui/icons-material/Delete";
import { Box } from "@mui/system";
import TextField from "@mui/material/TextField";
import Swal from "sweetalert2";

const style_ = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  borderRadius: 4,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
};

export default function Roomdata() {
  const [rows, setrows] = useState([]);
  const [open, setOpen] = useState(false);
  const [addon, setaddon] = useState(false);

  const handleClose = () => setOpen(false);

  const [id, setid] = useState("");
  const [idcard, setIdcard] = useState("");
  const [name, setName] = useState("");
  const [lastname, setLastname] = useState("");
  const [email, setEmail] = useState("");
  const [tel, setTel] = useState("");
  const [password, setPassword] = useState("");

  useEffect(() => {
    instance.post("employee_lsit").then((response) => {
      setrows(response.data);
      console.log(response.data);
    });
  }, []);
  const handleClick = (id_, idcard_, name_, lastname_, email_, tel_) => {
    setOpen(true);
    setaddon(false);

    setid(id_);
    setIdcard(idcard_);
    setName(name_);
    setLastname(lastname_);
    setEmail(email_);
    setTel(tel_);
  };

  const deleteClick = (id) => {
    Swal.fire({
      title: "",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: " ",
      cancelButtonText: " ",
    }).then((result) => {
      if (result.isConfirmed) {
        instance.delete(`delete_employee/${id}`).then((response) => {
          if (response.data.status === "ok") {
            Swal.fire({
              title: "Delete Employee Success",
              icon: "success",
              showConfirmButton: false,
            });
            setTimeout(function () {
              window.location.reload();
            }, 1000);
          }
        });
      }
    });
  };

  const Clickadd = (temp) => {
    if (temp === 1) {
      setaddon(true);
      setOpen(true);

      setid("");
      setIdcard("");
      setName("");
      setLastname("");
      setEmail("");
      setTel("");
    } else if (temp === 2) {
      setOpen(false);
      const JsonData = {
        idcard: idcard,
        name: name,
        lastname: lastname,
        email: email,
        tel: tel,
        password: password,
      };
      instance.post("add_employee", JsonData).then((response) => {
        if (response.data.status === "ok") {
          Swal.fire({
            title: "Add Employee Success",
            icon: "success",
            showConfirmButton: false,
          });
          setTimeout(function () {
            window.location.reload();
          }, 1000);
        }
      });
    }
  };

  const Clickupdate = () => {
    setOpen(false);
    const update_room = {
      id: id,
      idcard: idcard,
      name: name,
      lastname: lastname,
      email: email,
      tel: tel,
    };
    instance.put("update_employee", update_room).then((response) => {
      if (response.data.status === "ok") {
        Swal.fire({
          title: "Update Employee Success",
          icon: "success",
          showConfirmButton: false,
        });
        setTimeout(function () {
          window.location.reload();
        }, 1000);
      }
    });
  };
  return (
    <React.Fragment>
      <Box
        sx={{ display: "flex", justifyContent: "flex-end", p: 2 }}
        onClick={() => {
          Clickadd(1);
        }}
      >
        <Button
          variant="contained"
          color="success"
          sx={{ borderRadius: 5 }}
          // onClick={() => handleClick(row.password_room)}
        >
          
        </Button>
      </Box>

      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>ID</TableCell>
            <TableCell>ID Card</TableCell>
            <TableCell>Name</TableCell>
            <TableCell>Last Name</TableCell>
            <TableCell>Email</TableCell>
            <TableCell>Telephone</TableCell>
            <TableCell>Edit</TableCell>
            <TableCell>Delete</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.id}>
              <TableCell>{row.id}</TableCell>
              <TableCell>{row.idcard}</TableCell>
              <TableCell>{row.name}</TableCell>
              <TableCell>{row.lastname}</TableCell>
              <TableCell>{row.email}</TableCell>
              <TableCell>{row.tel}</TableCell>
              <TableCell>
                <Button
                  variant="contained"
                  color="secondary"
                  sx={{ borderRadius: 5 }}
                  onClick={() =>
                    handleClick(row.id, row.idcard, row.name, row.lastname, row.email, row.tel)
                  }
                >
                  
                </Button>
              </TableCell>
              <TableCell>
                <Button
                  variant="contained"
                  color="error"
                  sx={{ borderRadius: 5 }}
                  endIcon={<DeleteIcon />}
                  onClick={() => deleteClick(row.id)}
                >
                  
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <Modal open={open} onClose={handleClose}>
        <Box sx={style_}>
          <TextField
            id="idcard"
            label="idcard"
            value={idcard}
            variant="outlined"
            margin="normal"
            required
            fullWidth
            onChange={(e) => setIdcard(e.target.value)}
          />

          <TextField
            id="name"
            label="name"
            value={name}
            variant="outlined"
            margin="normal"
            fullWidth
            required
            onChange={(e) => setName(e.target.value)}
          />
          <TextField
            id="lastname"
            label="lastname"
            value={lastname}
            variant="outlined"
            margin="normal"
            fullWidth
            required
            onChange={(e) => setLastname(e.target.value)}
          />
          <TextField
            id="email"
            label="email"
            value={email}
            variant="outlined"
            margin="normal"
            fullWidth
            required
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            id="tel"
            label="tel"
            value={tel}
            variant="outlined"
            margin="normal"
            fullWidth
            required
            onChange={(e) => setTel(e.target.value)}
          />
           {addon && (
            <TextField
              id="password"
              label="password"
              value={password}
              variant="outlined"
              margin="normal"
              fullWidth
              required
              onChange={(e) => setPassword(e.target.value)}
            />
          )}
          <Box>
            {!addon && (
              <Button
                variant="contained"
                color="secondary"
                sx={{ borderRadius: 5, mr: 5 }}
                onClick={() => {
                  Clickupdate();
                }}
              >
                
              </Button>
            )}
            {addon && (
              <Button
                variant="contained"
                color="secondary"
                sx={{ borderRadius: 5, mr: 5 }}
                onClick={() => {
                  Clickadd(2);
                }}
              >
                
              </Button>
            )}

            <Button
              variant="contained"
              color="error"
              sx={{ borderRadius: 5 }}
              onClick={() => setOpen(false)}
            >
              
            </Button>
          </Box>
        </Box>
      </Modal>
      <style>{`
        table {
          border-collapse: separate;
          border-spacing: 0;
          min-width: 800px;
          width: 100%;
        }
        th {
          background-color: #f5f5f5;
          border: 1px solid #ddd;
          font-weight: bold;
          padding: 10px;
          text-align: left;
        }
        td {
          border: 1px solid #ddd;
          padding: 10px;
        }
        tr:nth-child(even) td {
          background-color: #f5f5f5;
        }
        tr:hover td {
          background-color: #fafafa;
        }
      `}</style>
    </React.Fragment>
  );
}
