import React from "react";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Navbar from "../Layout/Header";
import Typography from "@mui/material/Typography";
import {
  Box,
  Card,
  CardActions,
  CardContent,
  Button,
  Grid,
  CardMedia,
} from "@mui/material";
import Image from "../image/img5.png";

import Room1 from "../image/room1.jpg";


const styles = {
  paperContainer: {
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
};

const theme = createTheme();

const bookings = [
  {
    id: 1,
    name: "John Doe",
    date: "2022-04-15",
    time: "18:30",
    partySize: 4,
    room: {
      id: 1,
      name: "Deluxe Room",
      price: 199,
      description:
        "Spacious room with a king-size bed and stunning city views.",
      image: "../image/room1.jpg",
    },
  },
  {
    id: 2,
    name: "Jane Smith",
    date: "2022-04-16",
    time: "19:00",
    partySize: 2,
    room: {
      id: 2,
      name: "Superior Room",
      price: 149,
      description:
        "Comfortable room with a queen-size bed and modern amenities.",
      image: "../image/room1.jpg",
    },
  },
  {
    id: 3,
    name: "Bob Johnson",
    date: "2022-04-17",
    time: "20:00",
    partySize: 6,
    room: {
      id: 3,
      name: "Executive Suite",
      price: 399,
      description:
        "Luxurious suite with a separate living area and ocean views.",
      image: { Room1 },
    },
  },
  {
    id: 4,
    name: "Bob Johnson",
    date: "2022-04-17",
    time: "20:00",
    partySize: 6,
    room: {
      id: 3,
      name: "Executive Suite",
      price: 399,
      description:
        "Luxurious suite with a separate living area and ocean views.",
      image: { Room1 },
    },
  },
];

export default function Profile() {



  return (
    <ThemeProvider theme={theme}>
      <main>
        {/* Hero unit */}
        <Box style={styles.paperContainer}>
          <Navbar />
          <Box
            color="transparent"
            elevation={0}
            sx={{
              pt: 5,
              pb: 10,
            }}
          >
            <Typography
              variant="h2"
              align="center"
              color="common.white"
              gutterBottom
            >
              MY Room
            </Typography>
          </Box>
        </Box>
        <Box sx={{ p: 2, backgroundColor: "#14274A" }}>
          <Typography variant="h5" gutterBottom>
            Booking History
          </Typography>
          <Grid container spacing={2}>
            {bookings.map((booking) => (
              <Grid item xs={12} sm={6} md={4} key={booking.id}>
                <Card sx={{ height: "100%" }}>
                  <CardMedia
                    component="img"
                    height="140"
                    image={booking.room.image}
                    alt={booking.room.name}
                  />
                  <CardContent>
                    <Typography
                      variant="h6"
                      gutterBottom
                      sx={{ fontWeight: "bold" }}
                    >
                      Booking #{booking.id}
                    </Typography>
                    <Typography variant="subtitle1" sx={{ mb: 2 }}>
                      {booking.room.name}
                    </Typography>
                    <Typography sx={{ mb: 2 }}>
                      {booking.room.description}
                    </Typography>
                    <Typography variant="h6" sx={{ mb: 2 }}>
                      ${booking.room.price} / night
                    </Typography>
                    <Typography sx={{ mb: 2 }}>
                      <strong>Name: </strong>
                      {booking.name}
                    </Typography>
                    <Typography sx={{ mb: 2 }}>
                      <strong>Date: </strong>
                      {booking.date}
                    </Typography>
                    <Typography sx={{ mb: 2 }}>
                      <strong>Time: </strong>
                      {booking.time}
                    </Typography>
                    <Typography sx={{ mb: 2 }}>
                      <strong>Party Size: </strong>
                      {booking.partySize}
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Button size="small">View Details</Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Box>
      </main>
    </ThemeProvider>
  );
}
